<template>
    <div>

        <telegram-dlg v-if="telegramDlg" :telegramDlg="telegramDlg" @done="dlgDone()" />

        <email-dlg v-if="emailDlg" :emailDlg="emailDlg" @done="dlgDone()" />

        <v-speed-dial
            v-model="fab"
            bottom
            right
            direction="top"
            :open-on-hover="!$isMobile()"
            transition="slide-y-reverse-transition"
            class="contact mb-2"
            persistent
        >
            <template v-slot:activator>
                <v-btn
                    v-model="fab"
                    color="secondary darken-2"
                    fab
                    dark
                >
                    <v-icon v-if="fab">mdi-close</v-icon>
                    <v-icon v-else>mdi-message-text-outline</v-icon>
                </v-btn>
            </template>

            <v-tooltip color="info" left>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn fab small dark @click="telegramDlg = true" v-bind="attrs" v-on="on">
                        <v-img src="https://telegram.org/img/t_logo.png" width="25"></v-img>
                    </v-btn>
                </template>
                <span>Telegram</span>
            </v-tooltip>

            <v-tooltip color="info" left>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn fab small dark color="red" @click="emailDlg = true"  v-bind="attrs" v-on="on">
                        <v-icon>mdi-email-edit-outline</v-icon>
                    </v-btn>
                </template>
                <span>E-mail</span>
            </v-tooltip>

        </v-speed-dial>

    </div>
</template>

<script>

    const EmailDlg = () => import('@/components/contact/EmailDlg');
    const TelegramDlg = () => import('@/components/contact/TelegramDlg');

    export default {
        name: 'Contact',

        components: {
            'email-dlg': EmailDlg,
            'telegram-dlg': TelegramDlg
        },

        data() {
            return {
                fab: false,

                telegramDlg: false,
                emailDlg: false
            }
        },

        methods: {
            dlgDone() {
                this.telegramDlg = false;
                this.emailDlg = false;
            }
        }

    }

</script>

<style>

    .contact {
        position: fixed !important;
    }

</style>
